import $ from 'jquery';
let educardCount = 0;
let frontEndCardcount = 0;
let backEndCardcount = 0;
let OtherSkillCardcount = 0;


export const CreateOnscreeen = ()=>{
  $.fn.isOnScreen = function(){

    var win = $(window);

    var viewport = {
        top : win.scrollTop(),
        left : win.scrollLeft()
    };
    viewport.right = viewport.left + win.width();
    viewport.bottom = viewport.top + win.height();

    var bounds = this.offset();
    bounds.right = bounds.left + this.outerWidth();
    bounds.bottom = bounds.top + this.outerHeight();

    return (!(viewport.right < bounds.left || viewport.left > bounds.right || viewport.bottom < bounds.top || viewport.top > bounds.bottom));

};
}



window.addEventListener("scroll", Animation);





function Animation()
            {
              if($(".App-education-heading").isOnScreen()){
                
                $(".App-education-heading:visible").addClass("slide-up");
              
              }
              if($(".App-education-card").isOnScreen()  && !educardCount){
                educardCount++;
                $(".App-education-card:visible").addClass("slide-left");

              }


              if($("#frontEndCard").isOnScreen() && !frontEndCardcount){
                frontEndCardcount++;
                $("#frontEndCard:visible").addClass("slide-up-skill-card");
              }
              if($("#BackEndCard").isOnScreen() && !backEndCardcount){
                backEndCardcount++;
                $("#BackEndCard:visible").addClass("slide-up-skill-card");
              }
              if($("#OtherSkillCard").isOnScreen() && !OtherSkillCardcount){
                OtherSkillCardcount++;
                $("#OtherSkillCard:visible").addClass("slide-up-skill-card");
              }
              
            }

export const educardAnimChnage = ()=>{
  $(".App-education-card:visible").removeClass("slide-left");
}

export const FrontENdCardAnimChnage = ()=>{
  $("#frontEndCard").removeClass("slide-up-skill-card");
}
export const BackENdCardChnage = ()=>{
  $("#BackEndCard").removeClass("slide-up-skill-card");
}
export const OtherSkillCardAnimChnage = ()=>{
  $("#OtherSkillCard").removeClass("slide-up-skill-card");
}



