import React, { useState } from "react";
import "./projectcmp.css";

import Github from "../../asset/github.png"
import SortFirst from "../../asset/sort-First.svg";
import SortSecond from "../../asset/sortSecond.svg";
import PathFindingFirst from "../../asset/PathFindProj.svg";
import PathFindingSecond from "../../asset/PathFidnProj.png";

import { PATHFINDING, SORTVIZULIZER } from "./projname";
const ProjectCmp = ({
    description,
    weblink,
    gitlink,
    projname
}) => {
  const [secondShow, setSecondShow] = useState(false);

  const ImageClasses = ()=>{
    if(projname == SORTVIZULIZER){
        return{
            firstImage : SortFirst,
            secondImage : SortSecond
        }
    }else if(projname == PATHFINDING){
        return {
            firstImage : PathFindingFirst,
            secondImage : PathFindingSecond
        }
    }
  }

  const firstOrSecond = () => {
    if (!secondShow) {
      return (
        <div
          className="project--card--main--container imageclass project--card-first-anim"
          style={{backgroundImage: `url(${ImageClasses().firstImage})` }}
          onClick={() =>{
            setSecondShow(!secondShow);
            setTimeout(() => {
                setSecondShow(secondShow);
            }, 10000);
          } }
        ></div>
      );
    } else {
      return (
        <div className="project--card--second--container project--card--main--container project--card-second-anim"
        style={{backgroundImage : `url(${ImageClasses().secondImage})` }}
        >
          <div className="project--card--desc">
            {description}
          </div>
          <div className="project--card--weblink">
            <div className="project--card--website">
              <a href={weblink} target="_blank">Website</a>
            </div>
          </div>
          <div className="project--card--website">
              <a href={gitlink}  target="_blank">
                <img src={Github} style={{width : "40px" , height: "40px"}}/>
              </a>
            </div>
        </div>
      );
    }
  };
  return <div style={{ width: "100%", height: "100%" }}>{firstOrSecond()}</div>;
};

export default ProjectCmp;
